.psychologist {
  margin-top: 32px;
  padding-bottom: 100px;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}

.psychologistLi {
  display: flex;
  gap: 24px;
}
.psychologist_avatar {
  width: 96px;
  height: 96px;
  border-radius: 30px;
  border: 2px solid;
  border-color: rgb(252, 131, 44, 0.2);
  padding: 12px;
}
.card_expirience {
  display: flex;
  gap: 36px;
  padding-left: 16px;
  margin-bottom: 24px;
}
.card_rating {
  display: flex;
  justify-content: flex-end;
  gap: 32px;
}
.card_h {
  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  color: #8a8a89;
  margin-bottom: 8px;
}
.card_p {
  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  color: #8a8a89;
}
.card_name {
  font-family: Inter;
  font-weight: 600;
  font-size: 24px;
  line-height: 24px;
  color: #191a15;

  margin-bottom: 24px;
}
.card_span {
  font-family: Inter;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #191a15;
}
.psychologist_list {
  gap: 32px;
  display: flex;
  flex-direction: column;
}
.loadMore {
  display: flex;
  margin-left: auto;
  margin-right: auto;
  margin-top: 64px;
  justify-content: center;
  border: 1px solid;
  border-radius: 30px;
  padding: 14px 40px;

  font-family: Inter;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  background-color: #fc832c;
  color: #fbfbfb;
  cursor: pointer;
  transition: 0.3s;
}
.loadMore:hover {
  background-color: #f37113;
}
.card_price {
  color: #38cd3e;
}
.card_rating_price {
  font-family: Inter;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  gap: 8px;
  color: #191a15;
}

.bpyug {
  width: 44px;
  height: 44px;
  border-radius: 50%;
  background-color: #fc832c;
}
.card_reviews {
  display: flex;
  gap: 12px;
}
.review_name {
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  color: #191a15;
  margin-bottom: 4px;
}
.review_rating {
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 20px;
  color: #191a15;
  margin-bottom: 18px;
  align-items: center;
  display: flex;
  gap: 8px;
}
.review_comment {
  font-family: Inter;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  color: rgb(25, 26, 21, 0.5);
  margin-bottom: 4px;
}
.review_list {
  display: flex;
  flex-direction: column;
  gap: 25px;
  margin-top: 48px;
}
.review_appointment {
  display: flex;

  margin-right: auto;
  margin-top: 40px;
  justify-content: flex-start;
  border: 1px solid;
  border-radius: 30px;
  padding: 14px 40px;
  font-family: Inter;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  background-color: #fc832c;
  color: #fbfbfb;
  cursor: pointer;
  transition: 0.3s;
}
.review_appointment:hover {
  background-color: #f37113;
}
.card_readMore {
  font-family: Inter;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  color: #191a15;
  border: none;
  padding: 0px;
  text-decoration: underline;
  background-color: #fbfbfb;
  cursor: pointer;
  transition: 0.3s;
}
.card_readMore:hover {
  color: #f37113;
  transform: scale(110%);
}
.card_svgActive {
  position: relative;
  width: 30px;
  height: 20px;
  top: 10px;
  right: 50px;
}
@media screen and (max-width: 768px) {
  .psychologistLi {
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
    padding: 10px;
    gap: 0;
  }

  .card_svgActive {
    top: -115px;
    left: 50px;
  }
}
@media screen and (max-width: 548px) {
  .card_rating {
    display: inline-grid;
    gap: 5px;
    margin-left: 25%;
  }
}
